<template lang="pug">
  .sign-in
    .card.shadow.px-4.pt-4.pb-2.sign-in-form
      b-form(@submit="onSubmit" :class="{ isLoading }")
        .btn.btn-outline-primary.w-100(
          v-google-signin-button="googleSignInParams.client_id"
          @click.native="googleIsLoading = true")
          | Sign in with Google
        p.text-center.my-2
          | or

        b-alert(
          :show="error"
          variant="danger")
          | {{ error }}

        b-form-group(label="Email:" label-for="email")
          b-form-input#email(
            :state="$v.form.email.$dirty ? !$v.form.email.$error : null"
            v-model.lazy="form.email" type="email" required="" autofocus="" size="is-medium" placeholder="Email")
          b-form-invalid-feedback(v-if="!$v.form.email.required")
            | Please fill this field with a valid email address
          b-form-invalid-feedback(v-if="!$v.form.email.email")
            | Please fill this field with a valid email address
          b-form-invalid-feedback(v-if="!$v.form.email.exists")
            | This email does not exist in our database
        b-form-group(label="Password:" label-for="password")
          b-form-input#password(
            :state="$v.form.password.$dirty ? !$v.form.password.$error : null"
            @keyup.native.enter="onSubmit"
            v-model="form.password" type="password" required="" password-reveal="" size="is-medium" placeholder="Password")
          b-form-invalid-feedback
            | Please use at least 6 characters
        b-form-group.has-text-left.mt-3
          b-form-checkbox(v-model="form.checked")
            | Remember me
        .text-right.mt-4
          b-button#btn_sign_up(
            variant="primary"
            :class="{'is-loading': isLoading, 'is-disabled': isLoading}"
            @click="onSubmit($event)")
            | Sign in

      p.has-text-grey.mt-4
        router-link(:to="{name: 'SignUp'}") Sign up
        |   · 
        router-link(:to="{name: 'LostPasswordPage'}") Forgot Password
        |   · 
        a(href="mailto:guillaume@Mawa.io") Help!

</template>


<script>

import GoogleSignInButton from 'vue-google-signin-button-directive'
import { validationMixin } from 'vuelidate'
import { required, minLength, email } from 'vuelidate/lib/validators'
import { User } from '@/store/models/resources'

import { mapActions } from "vuex";

export default {
  name: "SignIn",
  directives: {
    GoogleSignInButton
  },
  data() {
    return {
      isLoading: false,
      gdprConcented: false,
      error: null,
      redirect: null,
      form: {
        pid: '',
        email: "",
        username: "",
        password: "",
        checked: false
      },
      googleIsLoading: false,
      googleSignInParams: {
        client_id: '476667578043-0tm7eoufgmi6m961qb2m77hhelj5ksl5.apps.googleusercontent.com',
        response_type: 'permission'
      }
    };
  },
  validations: {
    form: {
      email: {
        required,
        email,
        exists(email) {
          if (email === '') return false

          return new Promise(resolve => {
            User.checkEmail({email}).then(res => {
              if (res.body.status_code === 200) {
                resolve(true)
              } else {
                console.log(this.$v.form)
                resolve(false)
              }
            })
          })
        }
      },
      password: {
        required,
        minLength: minLength(6)
      },
    }
  },
  created() {
    this.form.pid = this.$cookie.get('pid')
  },

  methods: {
    ...mapActions(['setLayout', 'setUser', 'getFavorites']),

    onSubmit(evt) {
      this.$v.form.$touch()
      if (this.$v.form.$anyError) {
        console.log(this.$v.form)
        return
      }

      this.isLoading = true;
      evt.preventDefault();
      this.$auth
        .login({
          body: {user: this.form},
          redirect: this.redirect || { name: "NewService" },
          rememberMe: this.form.checked
        })
        .then(
          (res) => {
            console.log( res.body)
            this.setUser(res.body.data);
          },
          (res) => {
            const { message } = res.body
            this.error = null;

            this.$toast.error(message)
            this.isLoading = false;
          }
        );

    },

    OnGoogleAuthSuccess(access_token) {
      this.$store.dispatch("setUserFromGoogle", {
        access_token,
        pid: this.form.pid,
      }).then((res) => {
        if (!res.user.existed) {
          this.$mixpanel.alias(res.user.id+'');
        }
        this.getFavorites()
        this.$store.dispatch("setUser", res.user);
        this.$auth.user(res.user)
        this.$auth.token(null, res.token)
        this.$auth.watch.authenticated = true
        this.$auth.watch.loaded = true
        this.googleIsLoading = false;
        this.$cookie.set('rememberMe', true, 365)
        // this.$router.push({name: 'Home'})
        this.$router.push({name: 'FrontPage'})
      });
    },
    OnGoogleAuthFail(error) {
      console.log(error)
      this.googleIsLoading = false;
    }
  }
};
</script>


<style lang="scss">

  .sign-in {

    input {
      font-weight: 300;
    }

    label:not(.custom-control-label) {
      text-transform: uppercase;
      font-weight:    700;
      font-size:      0.9rem;
      color:          #636573
    }

    .isLoading {
      .was-validated .form-control:valid, .form-control.is-valid {
        border: 0 none;
        background-image: none;
      }
    }

    p.subtitle {
      padding-top: 1rem;
    }

    a {
      text-decoration: none;
    }

    .sign-in-form {
      width:  300px;
      margin: 100px auto;
    }

  }


</style>
